'use client';

import { useState } from 'react';
//import { signIn, signOut } from 'next-auth/react';

export default function UserPage() {
  const [user, setUser] = useState({
    username: 'ÂåøÂêçÁî®Êà∑',
    level: 'ÊôÆÈÄö‰ºöÂëò',
    balance: 100,
    rechargeHistory: [
      { date: '2023-01-01', amount: 50 },
      { date: '2023-02-01', amount: 30 },
    ],
  });

  const [isLoggedIn, setIsLoggedIn] = useState(true);
  const [isEditingUsername, setIsEditingUsername] = useState(false);
  const [newUsername, setNewUsername] = useState(user.username);

  const handleSignOut = () => {
    //signOut();
    setIsLoggedIn(false);
  };

  const handleUsernameClick = () => {
    if (isLoggedIn) {
      setIsEditingUsername(true);
    }
  };

  const handleUsernameChange = async () => {
    try {
      const response = await fetch('/api/user/update-username', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username: newUsername }),
      });

      if (response.ok) {
        setUser({ ...user, username: newUsername });
        setIsEditingUsername(false);
      } else {
        console.error('Êõ¥Êñ∞Áî®Êà∑ÂêçÂ§±Ë¥•');
      }
    } catch (error) {
      console.error('Êõ¥Êñ∞Áî®Êà∑ÂêçÊó∂ÂèëÁîüÈîôËØØ:', error);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleUsernameChange();
    } else if (e.key === 'Escape') {
      setIsEditingUsername(false);
      setNewUsername(user.username);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-4">
      <div className="w-full max-w-md bg-white rounded-xl shadow-md overflow-hidden relative p-6">
        <button onClick={() => window.location.href = '/home'} className="absolute top-4 left-4 text-2xl text-yellow-500">üè†</button>
        <h1 className="text-3xl font-bold mb-4 text-center text-yellow-600">Áî®Êà∑‰∏≠ÂøÉ</h1>
        <div className="flex items-center mb-6 justify-center">
          <img src="/assets/images/example1.jpg" alt="Áî®Êà∑Â§¥ÂÉè" className="w-32 h-32 rounded-full border-4 border-yellow-300 shadow-lg" />
          <div className="ml-4 text-center">
            {isLoggedIn ? (
              isEditingUsername ? (
                <div className="flex items-center">
                  <input
                    type="text"
                    value={newUsername}
                    onChange={(e) => setNewUsername(e.target.value)}
                    onKeyDown={handleKeyPress}
                    onBlur={handleUsernameChange}
                    autoFocus
                    className="text-xl font-semibold text-yellow-600 border-b-2 border-yellow-300 focus:outline-none bg-transparent"
                  />
                </div>
              ) : (
                <h2 
                  className="text-xl font-semibold text-yellow-600 cursor-pointer hover:text-yellow-700"
                  onClick={handleUsernameClick}
                  title="ÁÇπÂáª‰øÆÊîπÁî®Êà∑Âêç"
                >
                  {user.username}
                </h2>
              )
            ) : (
              <h2 className="text-xl font-semibold text-yellow-600">ÂåøÂêçÁî®Êà∑</h2>
            )}
            <p className="text-yellow-500">Á≠âÁ∫ß: {user.level}</p>
            {isLoggedIn && <p className="text-yellow-500">‰ΩôÈ¢ù: {user.balance} ÁèçÁè†Â∏Å</p>}
          </div>
        </div>
        {isLoggedIn ? (
          <div className="mb-6">
            <button className="block w-full p-2 bg-blue-500 text-white rounded-lg mb-2 hover:bg-blue-600 transition">Êü•ÁúãÂÖÖÂÄºËÆ∞ÂΩï</button>
          </div>
        ) : null}
        <div className="mt-4">
          {isLoggedIn ? (
            <>
              <button className="block w-full p-2 bg-yellow-500 text-white rounded-lg mb-2 hover:bg-yellow-600 transition">ÂÖÖÂÄº</button>
              <button onClick={handleSignOut} className="block w-full p-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition">Ê≥®ÈîÄ</button>
            </>
          ) : (
            <>
              <button onClick={() => window.location.href = '/auth/signup'} className="block w-full p-2 bg-blue-500 text-white rounded-lg mb-2 hover:bg-blue-600 transition">Ê≥®ÂÜå</button>
              <button onClick={() => window.location.href = '/auth/signin'} className="block w-full p-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition">ÁôªÂΩï</button>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

